.content-box {
  margin: 0 auto;
  width: 1506px;
  height: 100%;
}
.content-box .box-title {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  padding: 50px 0px;
  color: #727171;
}
.content-box .box-title.white {
  color: #fff;
}
.content-box .box-item {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.content-box .box-item .item {
  position: relative;
  display: inline-block;
  width: 363px;
  height: 645px;
}
.content-box .box-item .item:nth-child(n+2) {
  margin-left: 18px;
}
.content-box .box-item .reason-mask {
  height: 645px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  color: #fff;
  font-size: 14px;
}
.content-box .box-item .reason-mask .bg {
  margin-top: 485px;
  display: inline-block;
  background-color: #e1e1e1;
  height: 162px;
  width: 100%;
}
.content-box .box-item .reason-mask .bg h3 {
  color: #727171;
  font-size: 23px;
  margin-top: 28px;
}
.content-box .box-item .reason-mask .bg p {
  color: #727171;
  font-size: 14px;
  margin-top: 12px;
}
.content-box .box-item .box {
  width: 640px;
  height: 300px;
}
.content-box .box-item .box h2 {
  font-size: 32px;
  color: #727171;
}
.content-box .box-item .box p {
  font-size: 16px;
  color: #727171;
  margin-top: 5px;
}
.content-box .box-item.top {
  border-bottom: 1px solid #727171 !important;
  padding-bottom: 40px;
}
.content-box .box-item.down {
  margin: 40px 0px 65px;
}
.content-box .box-item.down h2 {
  margin-top: 195px;
}
.content-box .box-item.full {
  padding-bottom: 98px;
}
